import { supabase } from './supabase';
import { NetworkRetryManager } from './networkRecovery';
import { ErrorMessageGenerator, AppError } from './errorHandlingSystem';

/**
 *    Supabase  
 */
export class SupabaseWithErrorHandling {
  /**
   *   (  )
   */
  static async fetchData<T>(
    operation: () => Promise<{ data: T | null; error: any }>,
    context: string
  ): Promise<{ data: T | null; error: AppError | null }> {
    const result = await NetworkRetryManager.withRetry(
      async () => {
        const { data, error } = await operation();
        if (error) throw error;
        return data;
      },
      {
        maxAttempts: 3,
        baseDelay: 1000,
        retryableErrors: ['network', 'timeout', 'fetch failed', 'connection failed']
      },
      context
    );

    if (result.success) {
      return { data: result.data || null, error: null };
    } else {
      return { data: null, error: result.error || null };
    }
  }

  /**
   *   (  )
   */
  static async insertData<T>(
    operation: () => Promise<{ data: T | null; error: any }>,
    context: string
  ): Promise<{ data: T | null; error: AppError | null }> {
    const result = await NetworkRetryManager.withRetry(
      async () => {
        const { data, error } = await operation();
        if (error) throw error;
        return data;
      },
      {
        maxAttempts: 2, //     
        baseDelay: 500,
        retryableErrors: ['network', 'timeout', 'fetch failed']
      },
      context
    );

    if (result.success) {
      return { data: result.data || null, error: null };
    } else {
      return { data: null, error: result.error || null };
    }
  }

  /**
   *   (  )
   */
  static async updateData<T>(
    operation: () => Promise<{ data: T | null; error: any }>,
    context: string
  ): Promise<{ data: T | null; error: AppError | null }> {
    const result = await NetworkRetryManager.withRetry(
      async () => {
        const { data, error } = await operation();
        if (error) throw error;
        return data;
      },
      {
        maxAttempts: 2,
        baseDelay: 500,
        retryableErrors: ['network', 'timeout', 'fetch failed']
      },
      context
    );

    if (result.success) {
      return { data: result.data || null, error: null };
    } else {
      return { data: null, error: result.error || null };
    }
  }

  /**
   *   (  )
   */
  static async deleteData(
    operation: () => Promise<{ error: any }>,
    context: string
  ): Promise<{ error: AppError | null }> {
    const result = await NetworkRetryManager.withRetry(
      async () => {
        const { error } = await operation();
        if (error) throw error;
        return true;
      },
      {
        maxAttempts: 2,
        baseDelay: 500,
        retryableErrors: ['network', 'timeout', 'fetch failed']
      },
      context
    );

    if (result.success) {
      return { error: null };
    } else {
      return { error: result.error || null };
    }
  }

  /**
   *    (  )
   */
  static async authOperation<T>(
    operation: () => Promise<{ data: T | null; error: any } | { data: any; error: any }>,
    context: string
  ): Promise<{ data: T | null; error: AppError | null }> {
    const result = await NetworkRetryManager.withRetry(
      async () => {
        const { data, error } = await operation();
        if (error) throw error;
        return data;
      },
      {
        maxAttempts: 3,
        baseDelay: 1000,
        retryableErrors: ['network', 'timeout', 'fetch failed', 'auth']
      },
      context
    );

    if (result.success) {
      return { data: result.data || null, error: null };
    } else {
      return { data: null, error: result.error || null };
    }
  }
}

/**
 *     
 */
export const subscriptionErrorHandlers = {
  /**
   *   
   */
  async fetchSubscriptions(userId: string) {
    return SupabaseWithErrorHandling.fetchData(
      async () => {
        const result = await supabase
          .from('subscriptions')
          .select('*')
          .eq('user_id', userId)
          .eq('is_active', true)
          .order('created_at', { ascending: false });
        return result;
      },
      'fetch_subscriptions'
    );
  },

  /**
   *  
   */
  async createSubscription(subscriptionData: any) {
    return SupabaseWithErrorHandling.insertData(
      async () => {
        const result = await supabase
          .from('subscriptions')
          .insert(subscriptionData)
          .select()
          .single();
        return result;
      },
      'create_subscription'
    );
  },

  /**
   *  
   */
  async updateSubscription(id: string, updateData: any) {
    return SupabaseWithErrorHandling.updateData(
      async () => {
        const result = await supabase
          .from('subscriptions')
          .update(updateData)
          .eq('id', id)
          .select()
          .single();
        return result;
      },
      'update_subscription'
    );
  },

  /**
   *  
   */
  async deleteSubscription(id: string) {
    return SupabaseWithErrorHandling.deleteData(
      async () => {
        const result = await supabase
          .from('subscriptions')
          .delete()
          .eq('id', id);
        return result;
      },
      'delete_subscription'
    );
  },

  /**
   *   
   */
  async fetchProfile(userId: string) {
    return SupabaseWithErrorHandling.fetchData(
      async () => {
        const result = await supabase
          .from('profiles')
          .select('*')
          .eq('id', userId)
          .single();
        return result;
      },
      'fetch_profile'
    );
  },

  /**
   *   
   */
  async updateProfile(userId: string, updateData: any) {
    return SupabaseWithErrorHandling.updateData(
      async () => {
        const result = await supabase
          .from('profiles')
          .update(updateData)
          .eq('id', userId)
          .select()
          .single();
        return result;
      },
      'update_profile'
    );
  },

  /**
   *  
   */
  async fetchNotifications(userId: string) {
    return SupabaseWithErrorHandling.fetchData(
      async () => {
        const result = await supabase
          .from('notifications')
          .select('*')
          .eq('user_id', userId)
          .order('created_at', { ascending: false })
          .limit(50);
        return result;
      },
      'fetch_notifications'
    );
  },

  /**
   *  
   */
  async createNotification(notificationData: any) {
    return SupabaseWithErrorHandling.insertData(
      async () => {
        const result = await supabase
          .from('notifications')
          .insert(notificationData)
          .select()
          .single();
        return result;
      },
      'create_notification'
    );
  },

  /**
   *   
   */
  async markNotificationAsRead(notificationId: string) {
    return SupabaseWithErrorHandling.updateData(
      async () => {
        const result = await supabase
          .from('notifications')
          .update({ is_read: true })
          .eq('id', notificationId)
          .select()
          .single();
        return result;
      },
      'mark_notification_read'
    );
  }
};

/**
 *     
 */
export const authErrorHandlers = {
  /**
   * 
   */
  async signIn(credentials: { email: string; password: string }) {
    return SupabaseWithErrorHandling.authOperation(
      async () => {
        const result = await supabase.auth.signInWithPassword(credentials);
        return result;
      },
      'sign_in'
    );
  },

  /**
   * 
   */
  async signUp(userData: { email: string; password: string; user_metadata?: any }) {
    return SupabaseWithErrorHandling.authOperation(
      async () => {
        const result = await supabase.auth.signUp(userData);
        return result;
      },
      'sign_up'
    );
  },

  /**
   * 
   */
  async signOut() {
    return SupabaseWithErrorHandling.authOperation(
      async () => {
        const result = await supabase.auth.signOut();
        return { data: result, error: null };
      },
      'sign_out'
    );
  },

  /**
   *   
   */
  async getSession() {
    return SupabaseWithErrorHandling.authOperation(
      async () => {
        const result = await supabase.auth.getSession();
        return result;
      },
      'get_session'
    );
  },

  /**
   *  
   */
  async resetPassword(email: string) {
    return SupabaseWithErrorHandling.authOperation(
      async () => {
        const result = await supabase.auth.resetPasswordForEmail(email);
        return result;
      },
      'reset_password'
    );
  }
};

/**
 *   
 */
export const checkNetworkStatus = async () => {
  try {
    const { data, error } = await supabase.auth.getSession();
    if (error) {
      throw new Error('    .');
    }
    return true;
  } catch (error) {
    const appError = ErrorMessageGenerator.generate(error, 'network_check');
    throw appError;
  }
};