import React, { useState } from 'react';
import { SignUpData, ValidationError } from '../../types/auth';
import { validateTerms } from '../../lib/validation';

interface TermsStepProps {
  data: SignUpData;
  errors: ValidationError[];
  onChange: (field: keyof SignUpData, value: string | boolean) => void;
  onNext: () => void;
  onBack: () => void;
}

export const TermsStep: React.FC<TermsStepProps> = ({
  data,
  errors,
  onChange,
  onNext,
  onBack,
}) => {
  const [showTerms, setShowTerms] = useState(false);
  const [showPrivacy, setShowPrivacy] = useState(false);

  const getFieldError = (field: string) => {
    return errors.find(error => error.field === field)?.message;
  };

  const isStepValid = () => {
    return !validateTerms(data.agreeToTerms);
  };

  const handleNext = () => {
    if (isStepValid()) {
      onNext();
    }
  };

  return (
    <div className="space-y-6">
      <div>
        <h3 className="text-lg font-medium text-gray-900 mb-2"> </h3>
        <p className="text-sm text-gray-600">
               .
        </p>
      </div>

      <div className="space-y-4">
        <div className="flex items-start">
          <div className="flex items-center h-5">
            <input
              id="agreeToTerms"
              name="agreeToTerms"
              type="checkbox"
              checked={data.agreeToTerms}
              onChange={(e) => onChange('agreeToTerms', e.target.checked)}
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
            />
          </div>
          <div className="ml-3 text-sm">
            <label htmlFor="agreeToTerms" className="font-medium text-gray-900">
              <span className="text-red-600">*</span>    
            </label>
            <div className="mt-1 space-x-2">
              <button
                type="button"
                onClick={() => setShowTerms(!showTerms)}
                className="text-blue-600 hover:text-blue-500 underline"
              >
                 
              </button>
              <button
                type="button"
                onClick={() => setShowPrivacy(!showPrivacy)}
                className="text-blue-600 hover:text-blue-500 underline"
              >
                 
              </button>
            </div>
          </div>
        </div>

        {getFieldError('agreeToTerms') && (
          <p className="text-sm text-red-600">{getFieldError('agreeToTerms')}</p>
        )}

        <div className="flex items-start">
          <div className="flex items-center h-5">
            <input
              id="agreeToMarketing"
              name="agreeToMarketing"
              type="checkbox"
              checked={data.agreeToMarketing}
              onChange={(e) => onChange('agreeToMarketing', e.target.checked)}
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
            />
          </div>
          <div className="ml-3 text-sm">
            <label htmlFor="agreeToMarketing" className="font-medium text-gray-900">
                  ()
            </label>
            <p className="text-gray-500">
                     .
            </p>
          </div>
        </div>
      </div>

      {/*    */}
      {showTerms && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-11/12 md:w-3/4 lg:w-1/2 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
              <div className="max-h-96 overflow-y-auto text-sm text-gray-600 space-y-4">
                <p>
                  <strong>1 ()</strong><br />
                      ( "")       ,      .
                </p>
                <p>
                  <strong>2 ()</strong><br />
                  1. ""       .<br />
                  2. ""       .
                </p>
                <p>
                  <strong>3 ( )</strong><br />
                       :<br />
                  -     <br />
                  -   <br />
                  -     
                </p>
                <p>
                  <strong>4 ()</strong><br />
                           ,  , ,      .
                </p>
              </div>
              <div className="mt-6 flex justify-end">
                <button
                  onClick={() => setShowTerms(false)}
                  className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {showPrivacy && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-11/12 md:w-3/4 lg:w-1/2 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
              <div className="max-h-96 overflow-y-auto text-sm text-gray-600 space-y-4">
                <p>
                  <strong>1.    </strong><br />
                        :<br />
                  -     <br />
                  -     <br />
                  -      
                </p>
                <p>
                  <strong>2.   </strong><br />
                  - : , , <br />
                  - : ,  
                </p>
                <p>
                  <strong>3.    </strong><br />
                     (,        )
                </p>
                <p>
                  <strong>4.  </strong><br />
                    ,           .
                </p>
              </div>
              <div className="mt-6 flex justify-end">
                <button
                  onClick={() => setShowPrivacy(false)}
                  className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      <div className="flex justify-between pt-4">
        <button
          type="button"
          onClick={onBack}
          className="px-6 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
        >
          
        </button>
        <button
          type="button"
          onClick={handleNext}
          disabled={!isStepValid()}
          className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          
        </button>
      </div>
    </div>
  );
};