//   

export interface ScreenInfo {
  viewport: {
    width: number;
    height: number;
  };
  device: {
    width: number;
    height: number;
    ratio: number;
  };
  breakpoint: string;
  orientation: 'landscape' | 'portrait';
}

export interface OverflowElement {
  element: Element;
  tag: string;
  classes: string;
  scrollWidth: number;
  clientWidth: number;
  overflow: number;
}

export interface TouchTarget {
  element: Element;
  size: string;
  text: string;
}

export interface ImageStatus {
  total: number;
  loaded: number;
  failed: number;
  loading: number;
}

export interface LayoutHealthScore {
  screen: string;
  overflows: string;
  touchTargets: string;
  images: string;
}

//   
export const getCurrentBreakpoint = (): string => {
  const width = window.innerWidth;
  if (width < 640) return 'xs';
  if (width < 768) return 'sm';
  if (width < 1024) return 'md';
  if (width < 1280) return 'lg';
  if (width < 1536) return 'xl';
  return '2xl';
};

//   
export const getScreenInfo = (): ScreenInfo => {
  const info: ScreenInfo = {
    viewport: {
      width: window.innerWidth,
      height: window.innerHeight
    },
    device: {
      width: window.screen.width,
      height: window.screen.height,
      ratio: window.devicePixelRatio
    },
    breakpoint: getCurrentBreakpoint(),
    orientation: window.innerWidth > window.innerHeight ? 'landscape' : 'portrait'
  };
  
  console.log('  :', info);
  return info;
};

//   
export const findOverflowElements = (): OverflowElement[] => {
  const overflowing: OverflowElement[] = [];
  const allElements = document.querySelectorAll('*');
  
  allElements.forEach((el) => {
    if (el.scrollWidth > el.clientWidth || el.scrollHeight > el.clientHeight) {
      overflowing.push({
        element: el,
        tag: el.tagName,
        classes: el.className,
        scrollWidth: el.scrollWidth,
        clientWidth: el.clientWidth,
        overflow: el.scrollWidth - el.clientWidth
      });
    }
  });
  
  console.log('  :', overflowing);
  return overflowing;
};

//    
export const checkTouchTargets = (): TouchTarget[] => {
  const tooSmall: TouchTarget[] = [];
  const buttons = document.querySelectorAll('button, a, [role="button"]');
  
  buttons.forEach((btn) => {
    const rect = btn.getBoundingClientRect();
    const size = Math.min(rect.width, rect.height);
    
    if (size < 44) { // 44px    
      tooSmall.push({
        element: btn,
        size: `${rect.width}x${rect.height}`,
        text: btn.textContent?.trim() || btn.getAttribute('aria-label') || 'No text'
      });
    }
  });
  
  console.log('   :', tooSmall);
  return tooSmall;
};

//    
export const checkImageLoading = (): ImageStatus => {
  const images = document.querySelectorAll('img');
  const status: ImageStatus = {
    total: images.length,
    loaded: 0,
    failed: 0,
    loading: 0
  };
  
  images.forEach((img) => {
    if (img.complete) {
      if (img.naturalWidth === 0) {
        status.failed++;
      } else {
        status.loaded++;
      }
    } else {
      status.loading++;
    }
  });
  
  console.log('   :', status);
  return status;
};

//   
export const checkElementOverlap = (): Array<{element1: Element, element2: Element}> => {
  const overlapping: Array<{element1: Element, element2: Element}> = [];
  const elements = Array.from(document.querySelectorAll('*')).filter(el => 
    (el as HTMLElement).offsetWidth > 0 && (el as HTMLElement).offsetHeight > 0
  );

  for (let i = 0; i < elements.length; i++) {
    for (let j = i + 1; j < elements.length; j++) {
      const rect1 = elements[i].getBoundingClientRect();
      const rect2 = elements[j].getBoundingClientRect();

      if (rect1.right > rect2.left && 
          rect1.left < rect2.right && 
          rect1.bottom > rect2.top && 
          rect1.top < rect2.bottom) {
        overlapping.push({ element1: elements[i], element2: elements[j] });
      }
    }
  }

  return overlapping;
};

//    
export const healthCheck = (): LayoutHealthScore => {
  console.group('   ');
  
  const screenInfo = getScreenInfo();
  const overflows = findOverflowElements();
  const touchTargets = checkTouchTargets();
  const imageStatus = checkImageLoading();
  
  const score: LayoutHealthScore = {
    screen: screenInfo.breakpoint,
    overflows: overflows.length === 0 ? '' : ` ${overflows.length}`,
    touchTargets: touchTargets.length === 0 ? '' : ` ${touchTargets.length}`,
    images: imageStatus.failed === 0 ? '' : ` ${imageStatus.failed} `
  };
  
  console.log(' :', score);
  console.groupEnd();
  
  return score;
};

//    
export const runResponsiveTests = () => {
  const testSizes = [
    { name: 'iPhone SE', width: 375, height: 667 },
    { name: 'iPhone 14 Pro', width: 393, height: 852 },
    { name: 'iPad Air', width: 768, height: 1024 },
    { name: 'iPad Pro', width: 1024, height: 1366 },
    { name: 'Desktop', width: 1440, height: 900 },
    { name: '4K Monitor', width: 2560, height: 1440 }
  ];

  const results: Record<string, any> = {};

  testSizes.forEach((size) => {
    console.log(`\n ${size.name} (${size.width}x${size.height}) `);
    
    const issues: string[] = [];
    
    // 1.   
    if (document.body.scrollWidth > size.width) {
      issues.push(`   (${document.body.scrollWidth}px > ${size.width}px)`);
    }
    
    // 2.   
    const truncatedElements = document.querySelectorAll('.truncate');
    if (truncatedElements.length > 0) {
      issues.push(`${truncatedElements.length}   `);
    }
    
    // 3.   
    const overlapping = checkElementOverlap();
    if (overlapping.length > 0) {
      issues.push(`${overlapping.length}   `);
    }
    
    results[size.name] = {
      width: size.width,
      height: size.height,
      issues: issues,
      status: issues.length === 0 ? ' PASS' : ' FAIL'
    };
  });

  console.table(results);
  return results;
};

//    
export const createDebugObject = () => {
  if (typeof window !== 'undefined') {
    (window as any).debugResponsive = {
      getScreenInfo,
      getCurrentBreakpoint,
      findOverflowElements,
      checkTouchTargets,
      checkImageLoading,
      healthCheck,
      runResponsiveTests
    };

    //    
    window.addEventListener('resize', () => {
      console.log(`   : ${window.innerWidth}x${window.innerHeight} (${getCurrentBreakpoint()})`);
    });
  }
};