import { useState, useEffect, useCallback } from 'react';
import { AppError, ErrorMessageGenerator } from './errorHandlingSystem';

export interface RetryConfig {
  maxAttempts: number;
  baseDelay: number;
  maxDelay: number;
  backoffMultiplier: number;
  retryableErrors: string[];
}

export interface RetryResult<T> {
  success: boolean;
  data?: T;
  error?: AppError;
  attempts: number;
  totalTime: number;
}

/**
 *    
 */
export class NetworkRetryManager {
  private static defaultConfig: RetryConfig = {
    maxAttempts: 3,
    baseDelay: 1000,
    maxDelay: 10000,
    backoffMultiplier: 2,
    retryableErrors: [
      'network',
      'timeout',
      'connection failed',
      'fetch failed',
      'server error',
      '500',
      '502',
      '503',
      '504'
    ]
  };

  /**
   *      
   */
  private static calculateDelay(attempt: number, config: RetryConfig): number {
    const delay = config.baseDelay * Math.pow(config.backoffMultiplier, attempt - 1);
    return Math.min(delay, config.maxDelay);
  }

  /**
   *    
   */
  private static isRetryableError(error: any, config: RetryConfig): boolean {
    const errorMessage = this.extractErrorMessage(error).toLowerCase();
    return config.retryableErrors.some(pattern => 
      errorMessage.includes(pattern.toLowerCase())
    );
  }

  /**
   *   
   */
  private static extractErrorMessage(error: any): string {
    if (typeof error === 'string') return error;
    if (error?.message) return error.message;
    if (error?.error?.message) return error.error.message;
    return String(error);
  }

  /**
   *     
   */
  static async withRetry<T>(
    operation: () => Promise<T>,
    config: Partial<RetryConfig> = {},
    context?: string
  ): Promise<RetryResult<T>> {
    const finalConfig = { ...this.defaultConfig, ...config };
    const startTime = Date.now();
    let lastError: any;

    for (let attempt = 1; attempt <= finalConfig.maxAttempts; attempt++) {
      try {
        const data = await operation();
        return {
          success: true,
          data,
          attempts: attempt,
          totalTime: Date.now() - startTime
        };
      } catch (error) {
        lastError = error;
        
        //      
        if (attempt === finalConfig.maxAttempts || !this.isRetryableError(error, finalConfig)) {
          const appError = ErrorMessageGenerator.generate(error, context);
          return {
            success: false,
            error: appError,
            attempts: attempt,
            totalTime: Date.now() - startTime
          };
        }

        //   
        const delay = this.calculateDelay(attempt, finalConfig);
        console.log(`Retry attempt ${attempt}/${finalConfig.maxAttempts} in ${delay}ms...`);
        await this.sleep(delay);
      }
    }

    //        
    const appError = ErrorMessageGenerator.generate(lastError, context);
    return {
      success: false,
      error: appError,
      attempts: finalConfig.maxAttempts,
      totalTime: Date.now() - startTime
    };
  }

  /**
   *  
   */
  private static sleep(ms: number): Promise<void> {
    return new Promise(resolve => setTimeout(resolve, ms));
  }
}

/**
 *   
 */
export class NetworkMonitor {
  private static instance: NetworkMonitor;
  private listeners: Set<(isOnline: boolean) => void> = new Set();
  private isOnline: boolean = navigator.onLine;

  private constructor() {
    this.setupEventListeners();
  }

  static getInstance(): NetworkMonitor {
    if (!NetworkMonitor.instance) {
      NetworkMonitor.instance = new NetworkMonitor();
    }
    return NetworkMonitor.instance;
  }

  private setupEventListeners() {
    window.addEventListener('online', () => this.updateStatus(true));
    window.addEventListener('offline', () => this.updateStatus(false));
  }

  private updateStatus(isOnline: boolean) {
    this.isOnline = isOnline;
    this.listeners.forEach(listener => listener(isOnline));
    
    if (isOnline) {
      console.log('   .');
    } else {
      console.log('   .');
    }
  }

  /**
   *     
   */
  addListener(listener: (isOnline: boolean) => void) {
    this.listeners.add(listener);
    return () => this.listeners.delete(listener);
  }

  /**
   *    
   */
  isOnlineNow(): boolean {
    return this.isOnline;
  }

  /**
   *   
   */
  async testConnection(): Promise<boolean> {
    try {
      const response = await fetch('/api/health', { 
        method: 'HEAD',
        cache: 'no-cache'
      });
      return response.ok;
    } catch {
      return false;
    }
  }
}

/**
 * React :   
 */
export function useNetworkStatus() {
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [isConnecting, setIsConnecting] = useState(false);

  useEffect(() => {
    const monitor = NetworkMonitor.getInstance();
    const unsubscribe = monitor.addListener(setIsOnline);
    
    return () => {
      unsubscribe();
    };
  }, []);

  const testConnection = useCallback(async () => {
    setIsConnecting(true);
    try {
      const monitor = NetworkMonitor.getInstance();
      const isConnected = await monitor.testConnection();
      return isConnected;
    } finally {
      setIsConnecting(false);
    }
  }, []);

  return {
    isOnline,
    isConnecting,
    testConnection
  };
}

/**
 *    
 */
export function useNetworkRecovery() {
  const { isOnline, testConnection } = useNetworkStatus();
  const [recoveryAttempts, setRecoveryAttempts] = useState(0);

  const attemptRecovery = useCallback(async () => {
    setRecoveryAttempts(prev => prev + 1);
    
    //   
    if (!isOnline) {
      throw new Error('  .');
    }

    //   
    const isConnected = await testConnection();
    if (!isConnected) {
      throw new Error('   .');
    }

    return true;
  }, [isOnline, testConnection]);

  const resetRecoveryAttempts = useCallback(() => {
    setRecoveryAttempts(0);
  }, []);

  return {
    isOnline,
    recoveryAttempts,
    attemptRecovery,
    resetRecoveryAttempts
  };
}

