import React, { useState } from 'react';
import { useSupabase } from '../contexts/SupabaseContext';
import { AlertTriangle, RefreshCw, Database, Wifi } from 'lucide-react';

interface EmergencyTroubleshooterProps {
  isVisible: boolean;
  onClose: () => void;
}

export const EmergencyTroubleshooter: React.FC<EmergencyTroubleshooterProps> = ({
  isVisible,
  onClose
}) => {
  const { user, supabase } = useSupabase();
  const [currentStep, setCurrentStep] = useState<string>('');
  const [isRunning, setIsRunning] = useState(false);
  const [results, setResults] = useState<string[]>([]);

  const addResult = (message: string) => {
    setResults(prev => [...prev, `${new Date().toLocaleTimeString()}: ${message}`]);
  };

  const clearResults = () => {
    setResults([]);
  };

  //  1:     
  const handleScreenNotLoading = async () => {
    setIsRunning(true);
    setCurrentStep('    ...');
    clearResults();

    try {
      // 1.   
      addResult('    ...');
      const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
      const supabaseKey = process.env.REACT_APP_SUPABASE_ANON_KEY;
      
      if (!supabaseUrl || !supabaseKey) {
        addResult('    .');
        addResult(' .env.local  .');
        return;
      }
      addResult('    ');

      // 2.   
      addResult('    ...');
      if (!navigator.onLine) {
        addResult('   .');
        addResult('   .');
        return;
      }
      addResult('    ');

      // 3. Supabase  
      addResult(' Supabase   ...');
      const { error } = await supabase.from('profiles').select('count').limit(1);
      
      if (error) {
        addResult(` Supabase  : ${error.message}`);
        addResult(' Supabase   .');
        return;
      }
      addResult(' Supabase   ');

      // 4.    
      addResult('   .');
      addResult('   .');

    } catch (error) {
      addResult(`  : ${error instanceof Error ? error.message : '   '}`);
    } finally {
      setIsRunning(false);
      setCurrentStep('');
    }
  };

  //  2:     
  const handleNoDataAfterLogin = async () => {
    setIsRunning(true);
    setCurrentStep('    ...');
    clearResults();

    try {
      if (!user) {
        addResult('    .');
        return;
      }

      // 1.  
      addResult('   ...');
      const { error: profileError } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', user.id)
        .single();

      if (profileError) {
        if (profileError.code === 'PGRST116') {
          addResult('   .');
          addResult('   ...');
          
          //   
          const { error: createError } = await supabase
            .from('profiles')
            .insert({
              id: user.id,
              email: user.email,
              first_name: user.user_metadata?.first_name || '',
              last_name: user.user_metadata?.last_name || '',
              photo_url: user.user_metadata?.avatar_url || '',
              created_at: new Date().toISOString()
            })
            .select()
            .single();

          if (createError) {
            addResult(`   : ${createError.message}`);
          } else {
            addResult('   ');
          }
        } else {
          addResult(`   : ${profileError.message}`);
        }
      } else {
        addResult('   ');
      }

      // 2.   
      addResult('    ...');
      const { data: subscriptions, error: subError } = await supabase
        .from('subscriptions')
        .select('*')
        .eq('user_id', user.id);

      if (subError) {
        addResult(`    : ${subError.message}`);
        if (subError.message.includes('permission')) {
          addResult(' RLS    .');
        }
      } else {
        addResult(`     (${subscriptions?.length || 0})`);
      }

      // 3. RLS  
      addResult(' RLS   ...');
      try {
        await supabase
          .from('subscriptions')
          .select('count')
          .eq('user_id', user.id);
        addResult(' RLS  ');
      } catch (error) {
        addResult(' RLS   ');
        addResult(' Supabase Dashboard RLS  .');
      }

    } catch (error) {
      addResult(`  : ${error instanceof Error ? error.message : '   '}`);
    } finally {
      setIsRunning(false);
      setCurrentStep('');
    }
  };

  //  3:   
  const handleIntermittentIssues = async () => {
    setIsRunning(true);
    setCurrentStep('   ...');
    clearResults();

    try {
      // 1.   
      addResult('    ...');
      const { data: { session } } = await supabase.auth.getSession();
      
      if (!session) {
        addResult('   .');
        addResult('  .');
        return;
      }

      addResult('   ');
      addResult(`   : ${session.expires_at ? new Date(session.expires_at * 1000).toLocaleString() : 'Not set'}`);

      // 2.   
      addResult('    ...');
      try {
        await supabase.auth.refreshSession();
        addResult('   ');
      } catch (error) {
        addResult('   ');
        addResult('  .');
      }

      // 3.   
      addResult('    ...');
      if (!navigator.onLine) {
        addResult('   .');
      } else {
        addResult('   ');
      }

      // 4. Supabase   
      addResult(' Supabase    ...');
      try {
        await supabase.from('profiles').select('count').limit(1);
        addResult(' Supabase  ');
      } catch (error) {
        addResult(' Supabase  ');
        addResult('    .');
      }

    } catch (error) {
      addResult(`  : ${error instanceof Error ? error.message : '   '}`);
    } finally {
      setIsRunning(false);
      setCurrentStep('');
    }
  };

  if (!isVisible) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 max-w-2xl w-full mx-4 max-h-[80vh] overflow-y-auto">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-bold text-gray-900 flex items-center gap-2">
            <AlertTriangle className="w-6 h-6 text-red-500" />
               
          </h2>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600"
          >
            
          </button>
        </div>

        <div className="space-y-4 mb-6">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <button
              onClick={handleScreenNotLoading}
              disabled={isRunning}
              className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 disabled:opacity-50"
            >
              <div className="flex items-center gap-2 mb-2">
                <RefreshCw className="w-5 h-5 text-blue-500" />
                <span className="font-medium">  </span>
              </div>
              <p className="text-sm text-gray-600">    </p>
            </button>

            <button
              onClick={handleNoDataAfterLogin}
              disabled={isRunning}
              className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 disabled:opacity-50"
            >
              <div className="flex items-center gap-2 mb-2">
                <Database className="w-5 h-5 text-green-500" />
                <span className="font-medium"> </span>
              </div>
              <p className="text-sm text-gray-600">    </p>
            </button>

            <button
              onClick={handleIntermittentIssues}
              disabled={isRunning}
              className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 disabled:opacity-50"
            >
              <div className="flex items-center gap-2 mb-2">
                <Wifi className="w-5 h-5 text-yellow-500" />
                <span className="font-medium"> </span>
              </div>
              <p className="text-sm text-gray-600">  </p>
            </button>
          </div>
        </div>

        {currentStep && (
          <div className="mb-4 p-3 bg-blue-50 border border-blue-200 rounded-lg">
            <div className="flex items-center gap-2">
              <RefreshCw className="w-4 h-4 text-blue-500 animate-spin" />
              <span className="text-blue-700">{currentStep}</span>
            </div>
          </div>
        )}

        {results.length > 0 && (
          <div className="space-y-2">
            <div className="flex items-center justify-between">
              <h3 className="font-medium text-gray-900"> </h3>
              <button
                onClick={clearResults}
                className="text-sm text-gray-500 hover:text-gray-700"
              >
                
              </button>
            </div>
            <div className="bg-gray-50 p-3 rounded-lg max-h-60 overflow-y-auto">
              {results.map((result, index) => (
                <div key={index} className="text-sm text-gray-700 mb-1">
                  {result}
                </div>
              ))}
            </div>
          </div>
        )}

        <div className="mt-6 pt-4 border-t border-gray-200">
          <h4 className="font-medium text-gray-900 mb-2"> </h4>
          <div className="space-y-2 text-sm text-gray-600">
            <p>     </p>
            <p>   </p>
            <p>   </p>
            <p>   </p>
            <p>   <code className="bg-gray-100 px-1 rounded">window.supabaseMonitor.runFullDiagnostic()</code> </p>
          </div>
        </div>
      </div>
    </div>
  );
};