import React, { useState } from 'react';

const SupabaseConnectionTest = () => {
  const [testResults, setTestResults] = useState({
    environmentVariables: null,
    supabaseClient: null,
    databaseConnection: null,
    authSystem: null
  });
  const [loading, setLoading] = useState(false);

  const runConnectionTest = async () => {
    setLoading(true);
    const results = { ...testResults };

    try {
      // 1.  
      console.log('=== 1.   ===');
      const hasUrl = !!process.env.REACT_APP_SUPABASE_URL;
      const hasKey = !!process.env.REACT_APP_SUPABASE_ANON_KEY;
      
      results.environmentVariables = {
        status: hasUrl && hasKey ? 'success' : 'fail',
        details: {
          hasUrl,
          hasKey,
          url: hasUrl ? process.env.REACT_APP_SUPABASE_URL : 'Missing',
          keyLength: hasKey ? process.env.REACT_APP_SUPABASE_ANON_KEY.length : 0
        }
      };
      
      console.log(' :', results.environmentVariables);

      // 2. Supabase  
      console.log('=== 2. Supabase   ===');
      try {
        //  import supabase  
        const { supabase } = await import('../lib/supabase');
        
        results.supabaseClient = {
          status: 'success',
          details: {
            clientExists: !!supabase,
            authExists: !!supabase?.auth,
            fromExists: !!supabase?.from
          }
        };

        console.log('Supabase  :', results.supabaseClient);

        // 3.   
        console.log('=== 3.    ===');
        try {
          const { error } = await supabase
            .from('profiles')
            .select('*', { count: 'exact', head: true });

          results.databaseConnection = {
            status: error ? 'fail' : 'success',
            details: {
              error: error?.message || null,
              canQuery: !error,
              profilesTableExists: !error || !error.message?.includes('does not exist')
            }
          };

          console.log('  :', results.databaseConnection);
        } catch (dbError) {
          results.databaseConnection = {
            status: 'fail',
            details: { error: dbError.message }
          };
        }

        // 4.   
        console.log('=== 4.    ===');
        try {
          const { data: sessionData, error: sessionError } = await supabase.auth.getSession();
          
          results.authSystem = {
            status: sessionError ? 'fail' : 'success',
            details: {
              error: sessionError?.message || null,
              hasSession: !!sessionData?.session,
              userId: sessionData?.session?.user?.id || null,
              authWorking: !sessionError
            }
          };

          console.log('  :', results.authSystem);
        } catch (authError) {
          results.authSystem = {
            status: 'fail',
            details: { error: authError.message }
          };
        }

      } catch (importError) {
        results.supabaseClient = {
          status: 'fail',
          details: { error: `  : ${importError.message}` }
        };
        
        results.databaseConnection = { status: 'skip', details: { reason: 'Supabase   ' } };
        results.authSystem = { status: 'skip', details: { reason: 'Supabase   ' } };
      }

    } catch (error) {
      console.error('  :', error);
    }

    setTestResults(results);
    setLoading(false);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'success': return 'text-green-600 bg-green-100';
      case 'fail': return 'text-red-600 bg-red-100';
      case 'skip': return 'text-yellow-600 bg-yellow-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'success': return '';
      case 'fail': return '';
      case 'skip': return '';
      default: return '';
    }
  };

  return (
    <div className="max-w-4xl mx-auto p-6 bg-white rounded-lg shadow-lg">
      <div className="text-center mb-6">
        <h1 className="text-2xl font-bold text-gray-800 mb-2">
           Supabase   
        </h1>
        <p className="text-gray-600">
          Step-by-Step Supabase   
        </p>
      </div>

      <div className="text-center mb-6">
        <button
          onClick={runConnectionTest}
          disabled={loading}
          className={`px-6 py-3 rounded-lg font-medium ${
            loading
              ? 'bg-gray-400 text-white cursor-not-allowed'
              : 'bg-blue-600 text-white hover:bg-blue-700'
          }`}
        >
          {loading ? '   ...' : '   '}
        </button>
      </div>

      {Object.keys(testResults).some(key => testResults[key]) && (
        <div className="space-y-4">
          {/*   */}
          <div className="border rounded-lg p-4">
            <h3 className="text-lg font-semibold mb-3 flex items-center">
              <span className="mr-2"></span>
              1.  
              {testResults.environmentVariables && (
                <span className={`ml-2 px-2 py-1 rounded text-sm ${getStatusColor(testResults.environmentVariables.status)}`}>
                  {getStatusIcon(testResults.environmentVariables.status)} {testResults.environmentVariables.status}
                </span>
              )}
            </h3>
            {testResults.environmentVariables && (
              <div className="text-sm space-y-1">
                <div>SUPABASE_URL : {testResults.environmentVariables.details.hasUrl ? '' : ''}</div>
                <div>ANON_KEY : {testResults.environmentVariables.details.hasKey ? '' : ''}</div>
                {testResults.environmentVariables.details.hasUrl && (
                  <div className="text-gray-600">URL: {testResults.environmentVariables.details.url}</div>
                )}
                {testResults.environmentVariables.details.hasKey && (
                  <div className="text-gray-600">Key : {testResults.environmentVariables.details.keyLength} </div>
                )}
              </div>
            )}
          </div>

          {/* Supabase   */}
          <div className="border rounded-lg p-4">
            <h3 className="text-lg font-semibold mb-3 flex items-center">
              <span className="mr-2"></span>
              2. Supabase 
              {testResults.supabaseClient && (
                <span className={`ml-2 px-2 py-1 rounded text-sm ${getStatusColor(testResults.supabaseClient.status)}`}>
                  {getStatusIcon(testResults.supabaseClient.status)} {testResults.supabaseClient.status}
                </span>
              )}
            </h3>
            {testResults.supabaseClient && (
              <div className="text-sm space-y-1">
                {testResults.supabaseClient.status === 'success' ? (
                  <>
                    <div> : {testResults.supabaseClient.details.clientExists ? '' : ''}</div>
                    <div>Auth : {testResults.supabaseClient.details.authExists ? '' : ''}</div>
                    <div>Query : {testResults.supabaseClient.details.fromExists ? '' : ''}</div>
                  </>
                ) : (
                  <div className="text-red-600">: {testResults.supabaseClient.details.error}</div>
                )}
              </div>
            )}
          </div>

          {/*    */}
          <div className="border rounded-lg p-4">
            <h3 className="text-lg font-semibold mb-3 flex items-center">
              <span className="mr-2"></span>
              3.  
              {testResults.databaseConnection && (
                <span className={`ml-2 px-2 py-1 rounded text-sm ${getStatusColor(testResults.databaseConnection.status)}`}>
                  {getStatusIcon(testResults.databaseConnection.status)} {testResults.databaseConnection.status}
                </span>
              )}
            </h3>
            {testResults.databaseConnection && (
              <div className="text-sm space-y-1">
                {testResults.databaseConnection.status === 'success' ? (
                  <>
                    <div> : {testResults.databaseConnection.details.canQuery ? '' : ''}</div>
                    <div>profiles : {testResults.databaseConnection.details.profilesTableExists ? '' : ''}</div>
                  </>
                ) : testResults.databaseConnection.status === 'fail' ? (
                  <div className="text-red-600">: {testResults.databaseConnection.details.error}</div>
                ) : (
                  <div className="text-yellow-600">: {testResults.databaseConnection.details.reason}</div>
                )}
              </div>
            )}
          </div>

          {/*    */}
          <div className="border rounded-lg p-4">
            <h3 className="text-lg font-semibold mb-3 flex items-center">
              <span className="mr-2"></span>
              4.  
              {testResults.authSystem && (
                <span className={`ml-2 px-2 py-1 rounded text-sm ${getStatusColor(testResults.authSystem.status)}`}>
                  {getStatusIcon(testResults.authSystem.status)} {testResults.authSystem.status}
                </span>
              )}
            </h3>
            {testResults.authSystem && (
              <div className="text-sm space-y-1">
                {testResults.authSystem.status === 'success' ? (
                  <>
                    <div> : {testResults.authSystem.details.authWorking ? '' : ''}</div>
                    <div> : {testResults.authSystem.details.hasSession ? '' : '  ()'}</div>
                    {testResults.authSystem.details.userId && (
                      <div className="text-gray-600"> ID: {testResults.authSystem.details.userId}</div>
                    )}
                  </>
                ) : testResults.authSystem.status === 'fail' ? (
                  <div className="text-red-600">: {testResults.authSystem.details.error}</div>
                ) : (
                  <div className="text-yellow-600">: {testResults.authSystem.details.reason}</div>
                )}
              </div>
            )}
          </div>
        </div>
      )}

      <div className="mt-6 p-4 bg-blue-50 rounded-lg">
        <h4 className="font-medium text-blue-800 mb-2">   </h4>
        <div className="text-sm text-blue-700 space-y-1">
          <div>     Google OAuth   </div>
          <div>    .env     </div>
          <div>    src/lib/supabase.ts  </div>
          <div>    Supabase   </div>
          <div>    Supabase Auth  </div>
        </div>
      </div>
    </div>
  );
};

export default SupabaseConnectionTest;